/* ea-promo end */

/* ea-main-slider */
.ea-main-slider {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
	max-width: 1200px;
	// before slide load end

	&, &.slick-dotted {
		margin: 0 auto; }

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
		opacity: 1;
		visibility: visible;
 }		// after slide load end

	&::before,
	&::after {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		height: 100%;
		width: 1000px;
		background-color: #fff;
		opacity: 0.5;
		z-index: 1;
		pointer-events: none; }

	&::before {
		left: 100%; }

	&::after {
		right: 100%; }

	.slick-list {
		overflow: visible; }

	.slick-dots {
		bottom: 16px;

		li {

			&, .#{$user-content} & {
				padding: 4px 15px; }

			button {
				width: 10px;
				height: 10px;
				border-radius: 0;
				box-shadow: 0 0 3px rgba(255, 255, 255, 0.45);

				&, .ea-site & {
					background-color: $base-color-alt--ea; }

				&::before {
					$space: 1px;
					// =========
					left: $space;
					top: $space;
					height: calc(100% - #{$space*2});
					width: calc(100% - #{$space*2});
					border-radius: 0;
					box-shadow: none;
					background-color: #fff; }

				// hover
				//.no-touchevents &:hover
				//space: 2px
				/// ==========
				//::before
				//left: $space
				//top: $space
				//height: calc(100% - #{$space*2})
 } } }				//width: calc(100% - #{$space*2})

	.slick-arrow {
		@include size(50px);
		background-color: #fff; } }

.ea-main-slider__item {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end
	color: white;
	text-decoration: none;
	position: relative;
	transition: {
		property: opacity;
		duration: $animation-speed; }

	.slick-initialized &, .no-js & {
		// after slide load
		float: left;
		display: block;
 }		// after slide load end

	&::before {
		content: "";
		display: block;
		position: relative;
		@include ratio(1200, 472); } }

.ea-main-slider__inner {
	display: flex;
	position: absolute;
	left: 0;
	top: 0;
	@include size(100%);

	img {
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(cover); } }

.ea-main-slider__info,
.ea-main-slider__img {
	height: 100%;
	flex: 1 1 50%; }

.ea-main-slider__info {
	background-color: $base-color-alt--ea;
	padding: 3.5% 60px 6.5%;

	&.align-right {
		text-align: left;
		order: 1; }

	.btn-default {
		text-transform: uppercase;
		min-width: 224px; } }

.ea-main-slider__info__layout {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
	margin: 0 auto;
	height: 100%;
	max-width: 430px; }

.ea-main-slider__title {
	@include font-black;
	@include font(54px, 1);
	text-transform: uppercase;
	margin-bottom: 0.3em;

	@include media('<desk-hd') {
		font-size: 2.8vw; }

	@include media('<desk-md') {
		font-size: 3.5vw; } }

.ea-main-slider__text {
	@include font(19px, round-float(27/19, 2));
	text-transform: uppercase;
	margin-bottom: 0.5em;

	@include media('<desk-hd') {
		font-size: 1vw; }

	@include media('<desk-md') {
		font-size: 1.5vw; } }

/* ea-main-slider end */

.ea-main-section--share {
	.ea-main-section__layout {
		max-width: 1480px; } }

/* share */
.share {
	margin-bottom: $bottom-space--lg;

	&:last-child {
		margin-bottom: 0; } }

.share__thumbs {
	margin-bottom: 23px;
	@include clear;

	&:last-child {
		margin-bottom: 0; } }

/* share end */

/* share nav */
.share-nav {
	float: left;
	padding-right: 0;
	@include clear;
	@include clear-spacing;

	@include media('<mob-lg') {
		float: none;
		display: block;
		text-align: center; } }

.share-nav__item {
	@include display-inline-block-top;
	margin-right: -1px;
	@include font-bold;
	@include font(18px, 24px);
	letter-spacing: normal;
	text-align: center;

	a {
		display: block;
		position: relative;
		color: inherit;
		white-space: nowrap;
		text-decoration: none;
		padding: 12px 22px;
		min-width: 160px;
		border: 1px solid $border-color;
		background-color: white;

		@include media('<mob-lg') {
			min-width: 0;
			@include padding-horizontal(10px); }

		// hover
		&:hover {
			background-color: $bg-color-light; }

		// active
		&.active-tab {
			color: white;
			background-color: #666;
			border-color: #666;
			z-index: 1;
			cursor: default; } } }

/* share nav end */

/* share panels */
.share__panels {
	overflow: visible !important; }

.share__panel {
	opacity: 0;
	position: absolute;
	@include position(0);

	&.active-tab {
		z-index: 5 !important; } }

/* share panels end */

/* share list */
.share-list {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
	// before slide load end
	position: relative;
	border: solid $border-color;
	border-width: 1px 0;
	margin-bottom: 20px;

	&:last-child {
		margin-bottom: 0; }

	&::before, &::after {
		@include pseudo();
		top: 0;
		@include size(0, 100%);
		border-left: 1px solid $border-color;
		z-index: 5; }

	&::before {
		left: 0; }

	&::after {
		right: 0; }

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
		opacity: 1;
		visibility: visible;
 }		// after slide load end

	.slick-arrow {
		top: auto;
		bottom: 100%;
		transform: none;
		margin: 0 0 23px;
		@include size(50px);
		border: 1px solid $border-color;

		@include media('<mob-lg') {
			bottom: auto;
			top: 50%;
			margin: -20px 0 0; } }

	.slick-prev {
		left: auto;
		right: 49px;

		@include media('<mob-lg') {
			right: auto;
			left: 0; } } }

.share-list__item {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 } }		// after slide load end

.share-list__inner {
	//display: block
	position: relative;
	height: 100%;
	padding: 20px;
	text-decoration: none;
	color: inherit;
	text-align: center;
	border: solid $border-color;
	border-width: 0 1px 0 0;

	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;

	.share-list__item:first-child & {
		border-width: 0 1px; } }

.share-list__labels {
	position: absolute;
	@include position(20px);
	z-index: 5;
	@include clear-spacing; }

.share-list__label {
	@include display-inline-block-top;
	@include size(40px);
	padding-left: 0.05em;
	@include font-bold;
	@include font(12px, 40px);
	letter-spacing: 0.05em;
	text-align: center;
	background-color: $base-color--ea;

	&.label-sale {
		font-size: 30px; }

	&.label-hit {
		background-color: $base-color-alt--ea;
		color: white;
		text-transform: uppercase; }

	&.label-new {
		background-color: #85b700;
		color: white;
		text-transform: uppercase; } }

.share-list__content {
	margin-bottom: 13px;

	&:last-child {
		margin-bottom: 0; } }

.share-list__img {
	position: relative;
	max-width: 100%;
	width: 164px;
	margin: 0 auto 40px;

	&::before {
		content: "";
		display: block;
		width: 100%;
		@include ratio(1, 1); }

	&:last-child {
		margin-bottom: 0; }

	img {
		display: block;
		position: absolute;
		@include position(0);
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(contain); } }

.share-list__title {
	@include font(16px, round-float(19/16, 2));
	overflow: hidden;
	text-align: left;
	margin: 0 auto $bottom-space;
	max-width: 170px;
	transition: {
		property: color;
		duration: $animation-speed; }

	.no-touchevents a:hover & {
		color: $link-color; }

	&:last-child {
		margin-bottom: 0; } }

.share-list__price {
	min-height: 52px; // костыль

	.cur {
		@include font-bold;
		@include font(23px, round-float(28/23, 2));
		margin: 0 0 5px; }

	.old {
		@include font(15px, round-float(19/15, 2));
		color: $text-color-light;
		text-decoration: line-through; } }

/* share list end */

