/* contacts-accordion */
.contacts-accordion {
	margin-bottom: $bottom-space--md;
	border-bottom: 2px solid $outline-color;

	&:last-child {
		margin-bottom: 0; }

	& > a {
		display: block;
		position: relative;
		padding: 18px 0;
		@include font-bold;
		@include font(24px, round-float(32/24, 2));
		color: $text-color;
		text-decoration: none;
		transition-property: color;
		transition-duration: $animation-speed;

		&:hover {
			color: $base-color;

			.ea-site & {
				color: $base-color--ea; } }

		& > i {
			$this-icon-lines-width: 12px;
			$this-icon-lines-height: 2px;
			$this-icon-lines-color: $text-color;

			position: absolute;
			right: 0;
			top: 50%;
			@include size(16px, 10px);
			margin-top: -5px;
			backface-visibility: hidden;

			&::before, &::after {
				@include pseudo();
				bottom: 0;
				backface-visibility: hidden;
				@include size($this-icon-lines-width, $this-icon-lines-height);
				background-color: $this-icon-lines-color;
				transition-property: background-color;
				transition-duration: $animation-speed; }

			&::before {
				right: 50%;
				transform-origin: bottom right;
				transform: rotate(45deg); }

			&::after {
				left: 50%;
				transform-origin: bottom left;
				transform: rotate(-45deg); } }

		&:hover {
			& > i {
				&::before, &::after {
					background-color: $base-color;

					.ea-site & {
						background-color: $base-color--ea; } } } }

		&.is-open i {
			transform: rotate(0.5turn); }

		& > span {
			display: block;
			position: relative;
			margin-right: 25px; }

		& + div {
			padding: 18px 0;
			display: none;
			@include font(16px, round-float(24/16, 2));

			& > *:last-child {
				margin-bottom: 0; } } }

	h4 {
		@include font-bold;
		@include font(14px, round-float(24/14, 2));
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: inherit;
		margin-bottom: $bottom-space; }

	.cols {
		@include clear-spacing;
		@include grid-parent($grid-space);
		margin-bottom: $bottom-space;

		@include media('<desk-hd') {
			@include grid-parent($grid-space--sm); } }

	.col {
		@include grid-item(3, $grid-space);
		@include font(18px, round-float(32/18, 2));
		letter-spacing: normal;

		@include media('<desk-hd') {
			@include grid-item-responsive(3, $grid-space--sm); }

		@include media('<mob-lg') {
			@include grid-item-responsive(1, $grid-space--sm); }

		ul {
			margin: 0 0 10px;
			padding: 0;

			&:last-child {
				margin-bottom: 0; }

			li {
				padding: 0;
				margin: 0;

				&:last-child {
					margin-bottom: 0; }

				&::before {
					content: none; } } } } }

/* contacts-accordion end */
