//@include media('<tab-lg')
//	transform: none

.shutter {
	overflow: hidden; // необходим для скрытия скролла
	position: fixed;
	@include position(0, #{$header-width});
	@include size(100%, 100vh);
	min-width: 320px;
	max-width: $nav-width;
	//padding: 0 0 120px
	background: $bg-color-light;
	opacity: 0;
	visibility: hidden;
	z-index: 1000;

	@include media('<tab-lg') {
		left: 0;
		//top: #{$top-panel-height--md}
		//height: calc(100vh - #{$top-panel-height--md})
		max-width: none;
		padding-top: 0; } }

