.shutter__align {
	width: calc(100% + 100px);
	overflow-x: hidden;
	overflow-y: auto;
	height: 100vh;

	@include media('<tab-lg') {
		width: 100%; } }

.shutter__holder {
	//display: flex
	//flex-direction: column
	//justify-content: space-between
	//min-height: 100vh // ie поддерживает min-height для table
	display: table;
	table-layout: fixed;
	height: 100vh;
	width: #{$nav-width};

	@include media('<tab-lg') {
		width: 100%; }

	// для браузеров не поддерживающих display: flex
