@import "../utils/_variables";
@import "../utils/_mixins";
@import "../../libs/include-media/dist/_include-media";

/* order steps */
.order-steps {
	&, .#{$user-content} & {
		@include display-inline-block-middle;
		counter-reset: step;
		margin: 0;
		@include clear;

		@include media('<tab-sm') {
			display: block;
			width: 100%;
			padding: 0 0 1px 1px; }

		li {
			position: relative;
			@include display-inline-block-middle;
			counter-increment: step;
			margin: 0 0 0 85px;
			padding: 0;
			@include clear-spacing;
			color: $text-color-light;

			//+media('<tab-sm')
			//argin-left: 35px

			@include media('<tab-sm') {
				float: left;
				margin: 0 0 -1px -1px;
				border: 1px solid $border-color;
				width: 33.28%;
				padding: 8px 10px; }

			@include media('<mob-md') {
				display: block;
				float: none;
				width: auto;
				text-align: center; }

			&::before {
				position: relative;
				left: auto;
				top: auto;
				@include display-inline-block-middle;
				margin-right: 20px;
				content: counter(step);
				@include size(50px);
				border: 1px solid $outline-color;
				border-radius: 50%;
				background-color: transparent;
				@include font(18px, 48px);
				letter-spacing: normal;
				text-align: center;
				color: $text-color;

				//+media('<tab-sm')
				//include size(24px)
				//include font(12px, 22px)
				//argin-right: 6px

				@include media('<tab-sm') {
					content: none; } }

			&::after {
				@include pseudo();
				right: 100%;
				top: 50%;
				background: url(../../img/depict-arrow.png) no-repeat;
				background-size: contain;
				transform: translate(0, -50%);
				@include size(33px, 20px);
				margin-right: 20px;

				//+media('<tab-sm')
				//include size(10px, 10px)
				//ackground: none
				//order: solid $outline-color
				//order-width: 0 2px 2px 0
				//order-radius: 0
				//argin-right: 13px
				//ransform: translate(0, -50%) rotate(-45deg)
				///transform: rotate(45deg)

				@include media('<tab-sm') {
					content: none; } }

			&:first-child {
				margin-left: 0;

				@include media('<tab-sm') {
					margin-left: -1px; }

				&::after {
					content: none; } }

			& > span {
				@include display-inline-block-middle;
				letter-spacing: normal;
				@include font(18px, 22px);

				@include media('<mob-md') {
					@include font(16px, 20px);
					text-transform: uppercase; } } }

		li.active {
			@include font-bold;
			color: $text-color;

			@include media('<tab-sm') {
				background-color: $base-color;
				border-color: $base-color;
				z-index: 2;

				.ea-site & {
					background-color: $base-color--ea;
					border-color: $base-color--ea; } }

			&::before {
				background-color: $base-color;
				border-color: $base-color;

				.ea-site & {
					background-color: $base-color--ea;
					border-color: $base-color--ea; } } } } }

/* order steps end */

/* products basket */
.products-basket {
	margin-bottom: $bottom-space;

	@include media('<tab-sm') {
		@include margin-horizontal(-#{$text-layout-space-horizontal--sm}); }

	&:last-child {
		margin-bottom: 0; } }

.products-basket__list {
	display: table;
	width: 100%;
	//+clear-spacing
	border: solid $outline-color;
	border-width: 0 0 2px;
	margin-bottom: $bottom-space;

	@include media('<tab-sm') {
		display: block;
		border-width: 2px 0; }

	&:last-child {
		margin-bottom: 0; } }

.products-basket__item {
	display: table-row;
	position: relative;
	@include clear-spacing;
	border: 0 solid $outline-color;
	background-color: #{$bg-color};
	transition-property: background-color;
	transition-duration: $animation-speed;

	@include media('<tab-sm') {
		display: block;

		&:nth-child(odd) {
			background-color: $bg-color-light; } }

	@include media('>=tab-sm') {
		.no-touchevents & {
			&:hover, &:focus {
				background: $bg-color-light; } } } }

.products-basket__cell {
	//+display-inline-block-middle
	//width: round(100%/5)
	display: table-cell;
	letter-spacing: normal;
	vertical-align: middle;
	@include padding-vertical(12px);
	@include padding-horizontal(round($product-basket-cell-space/2));
	@include font($font-size, $line-height);
	border: solid $outline-color;
	border-width: 1px 0 0;

	@include media('<tab-lg') {
		@include font($font-size--sm, $line-height--sm); }

	@include media('<tab-sm') {
		display: block;
		width: auto !important;
		max-width: none !important;
		//+font($font-size, $line-height)
		padding: 5px $text-layout-space-horizontal--sm;
		border-width: 0;

		&:first-child {
			padding-top: 20px; }

		&:last-child {
			padding-bottom: 20px; } } }

.products-basket__cell[data-label] {
	@include media('<tab-sm') {
		position: relative;
		display: flex;
		//justify-content: space-between
		justify-content: flex-start;
		align-items: center;

		&::before {
			display: block;
			content: attr(data-label);
			width: $product-basket-label-width;
			min-width: $product-basket-label-width;
			padding-right: 20px;
			@include font-normal;
			//text-align: center
			@include font(12px, 16px);
			letter-spacing: 0.025em;
			text-transform: uppercase;

			@include media('<mob-md') {
				width: $product-basket-label-width--sm;
				min-width: $product-basket-label-width--sm; } } } }

.products-basket__head {
	@include media('<tab-sm') {
		display: none; }

	.products-basket__cell {
		@include font-bold;
		background-color: $bg-color-light;
		border-width: 0 0 1px; } }

.products-basket__product {
	width: 49.8%;
	text-align: left;

	@include media('<tab-sm') {
		@include clear; } }

.products-basket__figure {
	@include display-inline-block-middle;
	position: relative;
	width: #{$product-basket-figure-width};
	margin-right: #{$product-basket-figure-space};

	@include media('<tab-sm') {
		float: left;
		width: #{$product-basket-figure-width--sm};
		margin-right: 0; }

	@include media('<mob-md') {
		float: none;
		margin-bottom: #{$product-basket-figure-space}; }

	&:before {
		@include pseudo-block-relative();
		@include ratio(4, 3); } }

.products-basket__img {
	@include cover-abs();
	backface-visibility: hidden;

	img {
		display: block;
		max-height: 100%;
		max-width: 100%;
		@include centering-transform-rel(); } }

.products-basket__title {
	@include display-inline-block-middle;
	width: calc(100% - #{$product-basket-figure-width} - #{$product-basket-figure-space} - 6px);
	transition-property: color;
	transition-duration: $animation-speed;

	@include media('<tab-sm') {
		display: block;
		width: auto;
		margin-left: $product-basket-label-width; }

	@include media('<mob-md') {
		margin-left: 0; }

	a {
		text-decoration: none;
		color: inherit;
		transition: inherit;
		padding-top: 0.6em;

		.no-touchevents &:hover {
			color: $link-color; } } }

.products-basket__amount {
	.spinner {
		display: inline-block; } }

.products-basket__price,
.products-basket__sum {
	.cur {
		display: block;
		white-space: nowrap;
		font-size: 16px;

		@include media('<tab-sm') {
			white-space: normal; } } }

// .products-basket__price

.products-basket__sum {
	.cur {
		@include font-bold;
		font-size: 20px; } }

.products-basket__remove {
	text-align: center;
	max-width: 70px;

	@include media('<tab-sm') {
		//justify-content: space-between
		text-align: right; }

	.btn-remove {
		line-height: 1;
		vertical-align: middle;

		@include media('<tab-sm') {
			padding-right: 10px;

			& > i {
				@include size(32px);
				vertical-align: middle; }

			& > span {
				@include display-inline-block-middle;
				clip: auto;
				height: auto;
				margin: 0;
				padding: auto;
				position: relative;
				width: auto; } } } }

.product-basket__total {
	text-align: right;
	margin-bottom: $bottom-space;

	@include media('<mob-lg') {
		text-align: center; }

	&:last-child {
		margin-bottom: 0; }

	& > div {
		@include display-inline-block-middle; }

	.label {
		margin-right: 20px; }

	.total-price {
		@include font-bold; } }

.product-basket__footer {
	text-align: right;
	direction: rtl;

	@include media('<mob-lg') {
		text-align: center;
		direction: ltr; }

	.arr-wrap {
		margin: 0 -#{round-float($bottom-space/2, 1)};
		@include clear-spacing;

		@include media('<mob-lg') {
			@include margin-horizontal(0); } }

	[class*="btn-"] {
		margin: 0 #{round-float($bottom-space/2, 1)} #{$bottom-space};
		min-width: 250px;
		direction: ltr; }

	// for ordering form
	.ordering-form & {
		@include margin-horizontal(-$ordering-gap/2);

		@include media('<tab-lg') {
			@include margin-horizontal(0); }

		.arr-wrap {
			@include margin-horizontal(0);
			width: 50%;
			@include padding-horizontal($ordering-gap/2);

			@include media('<tab-lg') {
				width: auto;
				@include padding-horizontal(0);
				@include margin-horizontal(-#{round-float($bottom-space/2, 1)}); }

			@include media('<mob-lg') {
				@include margin-horizontal(0); } }

		[class*="btn-"] {
			min-width: 0;
			@include margin-horizontal(7px);
			width: calc(49.98% - 7px);

			&:nth-child(even) {
				margin-left: 0; }

			&:nth-child(odd) {
				margin-right: 0; } } }

	// for completion form
	.completion-form & {
		.arr-wrap {
			text-align: center; } }

	// for all
	.ordering-form & [class*="btn-"], & [class*="btn-"] {
		&:nth-child(even), &:nth-child(odd) {
			@include media('<tab-lg') {
				width: calc(49.98% - #{round-float($bottom-space/2, 1)*2});
				@include margin-horizontal(round-float($bottom-space/2, 1));
				min-width: 0; }

			@include media('<mob-lg') {
				display: block;
				width: 100%;
				@include push-auto; } } } }

/* products basket end */

/* ordering */
.ordering {
	@include margin-horizontal(-$ordering-gap/2);
	margin-bottom: $bottom-space;
	@include clear;

	&:last-child {
		margin-bottom: 0; } }

.ordering__col {
	float: left;
	width: 50%;
	@include padding-horizontal(round($ordering-gap/2));

	@include media('<tab-lg') {
		float: none;
		width: auto;
		margin-top: $bottom-space--md;

		&:first-child {
			margin-top: 0; } } }

.ordering__box {
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; } }

.ordering__title {
	@include font-bold;
	@include font($font-size-h3, $line-height-h3);
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	@include media('<mob-lg') {
		@include font($font-size-h3--sm, $line-height-h3--sm); } }

/* ordering end */

/* payment-list */
.payment-list {
	&, .#{$user-content} & {
		margin: 0 -7px $bottom-space;

		&:last-child {
			margin-bottom: 0; }

		li {
			padding: 0 7px 15px;
			margin: 0;

			&::before {
				content: none; } } }

	li {
		float: left;
		width: 50%;
		min-height: 165px;

		@include media('<mob-md') {
			float: none;
			display: block;
			width: auto;
			min-height: 100px; } }

	input[type="radio"] {
		display: none; }

	label {

		& > strong {
			display: block;
			margin-bottom: 0.2em;
			@include font-bold;
			@include font(18px, 21px);

			&:last-child {
				margin-bottom: 0; } }

		& > span {
			display: block;
			margin-bottom: 0.2em;
			@include font(16px, 19px);

			&:last-child {
				margin-bottom: 0; } }

		& > img {
			@include display-inline-block-middle;
			margin: 0 10px 14px 0; } }

	input[type="radio"] ~ label {
		display: block;
		height: 100%;
		border: 1px solid $border-color;
		padding: 12px 16px;

		&::before, &::after {
			content: none; }

		span {
			padding-left: 0;

			&::before, &::after {
				content: none; } } }

	// checked
	input[type="radio"]:checked ~ label {
		border-color: $base-color--ea;
		box-shadow: 0 0 0 1px $base-color--ea; } }

/* payment-list end */

/* radio-switcher */
.radio-switcher {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.radio-switcher__head {
	display: block;
	line-height: 1;
	padding: $bottom-space 0 0;

	&:first-child {
		padding-top: 0; }

	input[type="radio"] ~ label {
		&::before {
			border-color: $text-color-light; }

		em {
			@include font-normal;
			color: $text-color-light;
			border-bottom: 1px dashed $text-color-light; } }

	// hover
	input[type="radio"] ~ label {
		&:hover {
			&::before {
				border-color: $link-color; }

			em {
				color: $link-color;
				border-bottom-color: $link-color; } } }

	// checked
	input[type="radio"]:checked ~ label {
		cursor: default;

		&::before {
			border-color: $text-color; }

		em {
			@include font-bold;
			color: $text-color;
			border-bottom-color: transparent; } } }

.radio-switcher__panel {
	padding: $bottom-space 0 0;
	display: none;

	&:first-child {
		padding-top: 0; }

	.is-open + & {
		display: block; }

	ul {
		margin: 0 0 $bottom-space;
		padding-left: 20px;

		&:last-child {
			margin-bottom: 0; }

		li {
			margin: 10px 0 0;

			&:first-child {
				margin-top: 0; } } } }

/* radio-switcher end */

/* output */
.output {
	background-color: $bg-color-alt; }

.output__box {
	display: block;
	width: 440px;
	margin-left: auto;

	@include media('<tab-sm') {
		width: auto;
		margin: 0; } }

.output__list {
	margin-bottom: 10px;

	&:last-child {
		margin-bottom: 0; } }

.output__list__item, .output__footer__item {
	border: solid $border-color;
	border-width: 0 0 1px;
	padding: 8px 20px;

	&:first-child {
		border-top-width: 1px; }

	@include clear; }

.output__list__dt, .output__footer__dt {
	float: left;
	width: 50%;
	@include font(16px, 24px);
	@include truncate(none); }

.output__list__dd, .output__footer__dd {
	float: right;
	text-align: right;
	width: 50%;
	@include font(16px, 24px); }

.output__footer__item {
	border: none; }

.output__footer__dt {
	@include font-bold;
	@include font(16px, 36px);
	text-transform: uppercase; }

.output__footer__dd {
	@include font-bold;
	@include font(36px, 36px);

	@include media('<mob-lg') {
		font-size: 24px; } }

/* output end */

/* completion */
.completion {
	margin-bottom: $bottom-space--md;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space; }

	&:last-child {
		margin-bottom: 0; } }

.completion__figure {
	display: block;
	position: relative;
	@include size(100px);
	border: 4px solid $base-color;
	border-radius: 50px;
	@include push-auto;
	margin-bottom: $bottom-space--md;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space; }

	@include media('<tab-sm') {
		@include size(60px);
		border-width: 3px; }

	.ea-site & {
		border-color: $base-color--ea; }

	&::before, &::after {
		@include pseudo();
		height: 5px;
		background-color: $base-color;

		@include media('<tab-sm') {
			height: 4px; }

		.ea-site & {
			background-color: $base-color--ea; } }

	&::before {
		width: 24px;
		transform: rotate(45deg);
		left: 23px;
		top: 50px;

		@include media('<tab-sm') {
			width: 16px;
			left: 12px;
			top: 30px; } }

	&::after {
		width: 40px;
		transform: rotate(-45deg);
		left: 35px;
		top: 44px;

		@include media('<tab-sm') {
			width: 26px;
			left: 20px;
			top: 27px; } } }

.completion__title {
	text-align: center;
	@include font-bold;
	@include font($font-size-h2, $line-height-h2);
	margin-bottom: $bottom-space;

	@include media('<tab-lg') {
		@include font($font-size-h2--md, $line-height-h2--md); }

	@include media('<mob-lg') {
		@include font($font-size-h2--sm, $line-height-h2--sm); } }

.completion__note {
	@include font(18px, round-float(32/18, 2));
	text-align: center;
	margin-bottom: $bottom-space;

	@include media('<tab-sm') {
		font-size: 16px; }

	&:last-child {
		margin-bottom: 0; }

	a, a[href^="tel"] {
		color: $link-color;
		text-decoration: none !important;

		&:hover, .no-touchevents &:hover {
			color: $link-color;
			text-decoration: underline !important; } } }

/* completion end */
