@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* fotorama */
#toast-container {
	& > .toast-info {
		background: $bg-color-alt !important;
		border: 1px solid $border-color; }

	@include media('<mob-md') {
		width: 100%; }

	& > div {
		width: auto;
		max-width: 320px;

		&, &:hover {
			padding: 17px 20px;
			border-radius: 0;
			box-shadow: none;
			opacity: 1;
			color: $text-color;

			@include media('<mob-md') {
				padding: 8px 15px; } }

		@media (max-width: 768px) and (min-width: 481px) {
			width: auto;
			max-width: 240px; }

		@include media('<mob-md') {
			width: 100%;
			max-width: none; } } }

.toast-bottom-left {
	bottom: 14px;
	left: $sidebar-width + 20;

	@include media('<tab-lg') {
		left: 20px; }

	@include media('<mob-md') {
		left: 0;
		bottom: -6px; } }

.toast-title {
	@include font-bold;
	@include font(20px, 24px);
	margin-bottom: 5px;

	@include media('<mob-md') {
		@include font(18px, 22px); }

	&:last-child {
		margin-bottom: 0; } }

.toast-message {
	@include font-normal;
	@include font(18px, 24px);

	@include media('<mob-md') {
		@include font(16px, 20px); } }

/* fotorama end */
